<!-- 五角星组件 -->
<template>
  <div class="star" :class="starType">
    <span v-for="(itemClass,index) in itemClasses" :class="itemClass" class="star-item" :key="index"></span>
  </div>
</template>

<script>
const LENGTH = 5
const CLS_ON = 'on'
const CLS_HALF = 'half'
const CLS_OFF = 'off'

export default {
  props: {
    size: {
      type: Number
    },
    score: {
      type: Number
    }
  },
  computed: {
    starType() {
      return 'star-' + this.size
    },
    itemClasses() {
      let result = []
      let score = Math.floor(this.score * 2) / 2  // 结果不是整数，就是1.5,2.5(小数部分是5)
      let hasDecimal = score % 1 !== 0 // 判断是否有小数点
      let integer = Math.floor(score) // 整数部分
      for (let i = 0; i < integer; i++) {
        result.push(CLS_ON)
      }
      if (hasDecimal) {
        result.push(CLS_HALF)
      }
      while (result.length < LENGTH) {
        result.push(CLS_OFF)
      }
      return result

      // 方法2
      // let result = []
      // let hasDecimal = this.score % 1 !== 0  // 判断是否有小数点
      // let integer = Math.floor(this.score)  // 整数部分
      // for (let i = 0; i < integer; i++) {
      //   result.push(CLS_ON)
      // }
      // if (hasDecimal) {
      //   let larger = Math.floor((this.score - integer) * 10) >= 5
      //   if (larger) {
      //     result.push(CLS_HALF)
      //   }
      // }
      // while (result.length < LENGTH) {
      //   result.push(CLS_OFF)
      // }
      // return result
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../common/styles/mixin.scss';
.star{
  font-size:0;
  .star-item{
    display:inline-block;
    background-repeat:no-repeat;
  }
  &.star-48{
    .star-item{
      width:20px;
      height:20px;
      margin-right:22px;
      background-size:20px 20px;
      &:last-child{margin-right:0;}
      &.on{
        @include bg-image('star48_on');
      }
      &.half{
        @include bg-image('star48_half');
      }
      &.off{
        @include bg-image('star48_off');
      }
    }
  }
  &.star-36{
    .star-item{
      width:15px;
      height:15px;
      margin-right:6px;
      background-size:15px 15px;
      &:last-child{margin-right:0;}
      &.on{
        @include bg-image('star36_on');
      }
      &.half{
        @include bg-image('star36_half');
      }
      &.off{
        @include bg-image('star36_off');
      }
    }
  }
  &.star-24{
    .star-item{
      width:10px;
      height:10px;
      margin-right:3px;
      background-size:10px 10px;
      &:last-child{margin-right:0;}
      &.on{
        @include bg-image('star24_on');
      }
      &.half{
        @include bg-image('star24_half');
      }
      &.off{
        @include bg-image('star24_off');
      }
    }
  }
}
</style>



