<template>
  <div class="seller" ref="seller">
    <div class="seller-wrapper">
      <div class="overview">
        <div class="title">{{seller.name}}</div>
        <div class="desc border-1px">
          <star :size="36" :score="seller.score"></star>
          <span class="text">({{seller.ratingCount}})</span>
          <span class="text">月售{{seller.sellCount}}单</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
              <span class="stress">{{seller.minPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryTime}}</span>元
            </div>
          </li>
        </ul>
        <!-- 收藏 -->
        <div class="favorite" @click="toggleFavorite">
          <span class="icon-favorite" :class="{'active':favorite}"></span>
          <span class="text">{{favoriteText}}</span>
        </div>
      </div>
      <split></split>
      <!-- 公告与活动 -->
      <div class="bulletin">
        <h1 class="title">公告与活动</h1>
        <div class="content-wrapper border-1px">
          <p class="content">{{seller.bulletin}}</p>
        </div>
        <ul v-if="seller.supports" class="supports">
          <li class="support-item border-1px" v-for="(item,index) in seller.supports">
            <icon :size="4" :type="seller.supports[index].type"></icon>
            <span class="text">{{seller.supports[index].description}}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <!-- 商家实景 -->
      <div class="pics">
        <div class="title">商家实景</div>
        <div class="pic-wrapper" ref="picWrapper">
          <ul class="pic-list" ref="picList">
            <li class="pic-item" v-for="pic in seller.pics">
              <img :src="pic" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <!-- 商家信息 -->
      <div class="info">
        <h1 class="title border-1px">商家信息</h1>
        <ul>
          <li class="info-item border-1px" v-for="info in seller.infos">{{info}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll'
import star from '@/components/star/star'
import split from '@/components/split/split'
import icon from '@/components/icon/icon.vue'
import {saveToLocal, loadFromLocal} from '@/common/js/util'

export default {
  props: {
    seller: {
      type: Object
    }
  },
  data() {
    return {
      // favorite: false   // 本地固定死的值
      favorite: (() => {   // 读取localStorage本地值
        return loadFromLocal(this.seller.id, 'favorite', false)
      })()
    }
  },
  computed: {
    favoriteText() {
      return this.favorite ? '已收藏' : '收藏'
    }
  },
  watch: {
    'seller'() {   // 想后端请求数据的时候监听seller变化
      this.$nextTick(() => {
        this._initScroll()
        this._initPics()
      })
    }
  },
  mounted() {  // DOM渲染好以后
    this.$nextTick(() => {   // 不是请求数据的时候可以滚动
      this._initScroll()
      this._initPics()
    })
  },
  methods: {
    toggleFavorite() {  // 点击收藏事件
      if (!event._constructed) {
        return
      }
      this.favorite = !this.favorite
      saveToLocal(this.seller.id, 'favorite', this.favorite)  // 本地localStorage存储
      console.log(localStorage)
    },
    _initScroll() {   // 初始化上下滚动
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.seller, {
          click: true
        })
      } else {
        this.scroll.refresh()
      }
    },
    _initPics() {  // 初始化图片左右滚动
      if (this.seller.pics) {   // 如果有图片
        let picWidth = 120  // li的宽度
        let margin = 6      // li的margin
        let width = (picWidth + margin) * this.seller.pics.length - margin
        this.$refs.picList.style.width = width + 'px'
        this.$nextTick(() => {
          if (!this.picScroll) {
            this.picScroll = new BScroll(this.$refs.picWrapper, {
              scrollX: true,   // 开启横向滚动
              eventPassthrough: 'vertical'  // 希望横向模拟横向滚动，而纵向的滚动还是保留原生滚动
            })
          } else {
            this.picScroll.refresh()
          }
        })
      }
    }
  },
  components: {
    star,
    split,
    icon
  }
}
</script>
<style scoped lang="scss">
@import "../../common/styles/mixin.scss";
.seller{
  position:absolute;
  top:174px;
  bottom:0;
  left:0;
  width:100%;
  overflow:hidden;
  .overview{
    position:relative;
    padding:18px;
    .title{
      margin-bottom:8px;
      line-height: 14px;
      color:rgb(7,17,27);
      font-size:14px;
    }
    .desc{
      padding-bottom:18px;
      @include border-1px(rgba(7,17,27,0.1));
      font-size:0;
      .star{
        display:inline-block;
        margin-right:8px;
        vertical-align:top;
      }
      .text{
        display:inline-block;
        margin-right:12px;
        line-height: 18px;
        vertical-align:top;
        font-size:10px;
        color:rgb(77,85,93);
      }
    }
    .remark{
      display:flex;
      padding-top:18px;
      .block{
        flex:1;
        text-align:center;
        border-right:1px solid rgba(7,17,27,0.1);
        &:last-child{
          border:none;
        }
        h2{
          margin-bottom:4px;
          line-height: 10px;
          font-size:10px;
          color:rgb(147,153,159);
        }
        .content{
          line-height: 24px;
          font-size:10px;
          color:rgb(7,17,27);
          .stress{
            font-size:24px;
          }
        }
      }
    }
    /* 收藏 */
    .favorite{
      position: absolute;
      width: 50px;
      right: 11px;
      top: 18px;
      text-align: center;
      .icon-favorite{
        display: block;
        margin-bottom: 4px;
        line-height: 24px;
        font-size: 24px;
        color: #d4d6d9;
        &.active{
          color: rgb(240, 20, 20);
        }
      }
      .text{
        line-height: 10px;
        font-size: 10px;
        color: rgb(77, 85, 93);
      }
    }
  }
  /* 公告与活动 */
  .bulletin{
    padding:18px 18px 0 18px;
    .title{
      margin-bottom:8px;
      line-height: 14px;
      color: rgb(7,17,27);
      font-size:14px;
    }
    .content-wrapper{
      padding:0 12px 16px 12px;
      @include border-1px(rgba(7, 17, 27, 0.1));
      .content{
        line-height: 24px;
        font-size:12px;
        color:rgb(240,20,20);
      }
    }
    .supports{
      .support-item{
        padding:16px 12px;
        @include border-1px(rgba(7,17,27,0.1));
        font-size:0;
        &:last-child{
          @include border-none()
        }
        .text{
          line-height: 16px;
          font-size: 12px;
          color: rgb(7, 17, 27);
        }
      }
    }
  }
  /* 商家实景 */
  .pics{
    padding:18px;
    .title{
      margin-bottom:12px;
      line-height: 14px;
      color:rgb(7,17,27);
      font-size:14px;
    }
    .pic-wrapper{
      width:100%;
      overflow: hidden;
      white-space:nowrap;
      .pic-list{
        font-size:0;
        .pic-item{
          display:inline-block;
          margin-right:6px;
          width:120px;
          height:90px;
          &:last-child{
            margin:0;
          }
        }
      }
    }
  }
  /* 商家信息 */
  .info{
    padding: 18px 18px 0 18px;
    color: rgb(7, 17, 27);
    .title{
      padding-bottom: 12px;
      line-height: 14px;
      @include border-1px(rgba(7, 17, 27, 0.1));
      font-size: 14px;
    }
    .info-item{
      padding: 16px 12px;
      line-height: 16px;
      @include border-1px(rgba(7, 17, 27, 0.1));
      font-size: 12px;
      &:last-child{
        @include border-none()
      }
    }
  }
}
</style>



